    onSelectClick(demo) {
        if(demo.isPanorama)
            window.location = `/panoramaViewer?panoramaCollectionId=${demo.id}`;
        else
            window.location = `/modelViewer?backgroundImage=${demo.backgroundImage}&productId=${demo.id}&showbackground=true`;
    }

    renderGroup(name) {
        return (
            <div className="std-page">
                <div className='demo-group-container'>
                    {
                        this.state[name].map((demo, i) => {
                            return (
                                <div key={i} className='demo-card'>
                                    <div>
                                        <img
                                            src={demo.url}
                                            alt=''
                                            onClick={() => this.onSelectClick(demo)} />
                                    </div>
                                    <div style={{ textAlign: 'center' }}>{demo.name}</div>
                                </div>
                            )
                        })
                    }
                </div>
            </div>
        );
    }

